
import { defineComponent } from 'vue';
import Time from '@/components/Time.vue';
import axios from 'axios';
import dayjs from 'dayjs';
import weekOfYear from 'dayjs/plugin/weekOfYear';
dayjs.extend(weekOfYear)

type ServerData = {
  abbreviation: string;
  client_ip: string;
  datetime: string;
  day_of_week: number;
  day_of_year: number;
  dst: boolean;
  dst_from: string | null;
  dst_offset: number;
  dst_until: string | null;
  raw_offset: number;
  timezone: string;
  unixtime: number;
  utc_datetime: string
  utc_offset: string;
}

type LocationData = {
  status: string;
  country: string;
  countryCode: string;
  region: string;
  regionName: string;
  city: string;
  zip: string;
  lat: number;
  lon: number;
  timezone: string;
  isp: string;
  org: string;
  as: string;
  query: string;
}

type Data = {
  serverData: ServerData;
  locationData: LocationData;
  offset: string;
}

export default defineComponent({
  name: 'Home',
  data() {
    return {
      serverData: {},
      locationData: {},
      offset: '',
    } as Data;
  },
  async beforeCreate() {
    const { data: locationData } = await axios.get('http://ip-api.com/json');
    this.locationData = locationData;
    const { data: serverData } = await axios.get<ServerData>(`http://worldtimeapi.org/api/timezone/${this.locationData?.timezone}`);
    this.serverData = serverData;
    this.setOffset();
  },
  components: {
    Time,
  },
  methods: {
    setOffset() {
      const offset = Date.now() - +new Date(this.serverData?.datetime || '');
      const ms = dayjs(offset).format('SSS');
      const sec = dayjs(offset).format('ss');
      if (offset >= 1000) {
        this.offset = `${sec} sec ${ms} ms`;
      } else {
        this.offset = `${ms} ms`;
      }
    },
  },
  computed: {
    formattedDate() {
      return dayjs().format('dddd, DD MMMM YYYY')
    },
    week() {
      return dayjs().week() - 1;
    },
  },
});
